import buildDebug from 'debug'
import { and, between, eq, isNull, sql, sum } from 'drizzle-orm'
import { Logger } from '@verdaccio/types'

import { type Database } from '../db'
import { downloads } from '../db/schema'
import { getPackageInfoFromFilename, getISODates } from './utils'

const debug = buildDebug('verdaccio:plugin:storage:sql')

export type Downloads = {
  date: string
  count: number
}
export type DownloadsByVersion = {
  version: string
  count: number
}

export class DownloadsService {
  private db: Database
  private logger: Logger

  public constructor(database: Database, logger: Logger) {
    this.db = database
    this.logger = logger
  }

  public async increment(filename: string): Promise<void> {
    const { name, version } = getPackageInfoFromFilename(filename)

    const now = new Date()
    const year = now.getFullYear()
    const month = now.getMonth() + 1
    const today = now.toISOString().split('T')[0]

    // Daily by version; daily, monthly, yearly, and grand totals
    const data = [
      {
        timeslice: 'd',
        year,
        month,
        date: today,
        name,
        version,
        count: 1,
      },
      {
        timeslice: 'd',
        year,
        month,
        date: today,
        name: null,
        version: null,
        count: 1,
      },
      {
        timeslice: 'm',
        year,
        month,
        date: null,
        name: null,
        version: null,
        count: 1,
      },
      {
        timeslice: 'y',
        year,
        month: null,
        date: null,
        name: null,
        version: null,
        count: 1,
      },
      {
        timeslice: 't',
        year: null,
        month: null,
        date: null,
        name: null,
        version: null,
        count: 1,
      },
    ]

    try {
      await this.db
        .insert(downloads)
        .values(data)
        .onConflictDoUpdate({
          target: [downloads.timeslice, downloads.year, downloads.month, downloads.date, downloads.name, downloads.version],
          set: { count: sql`${downloads.count} + 1` },
        })

      debug('downloads incremented successfully')
    }
    catch (error) {
      debug('downloads error: %o', error)
    }
  }

  public async getDownloads(timeslice: string, start: string, end?: string): Promise<Downloads[] | null> {
    const [startDate, endDate] = getISODates(start, end)

    const data = await this.db
      .select({
        date: downloads.date,
        count: sum(downloads.count),
      })
      .from(downloads)
      .where(and(
        eq(downloads.timeslice, timeslice),
        between(downloads.date, startDate, endDate),
        isNull(downloads.name)))
      .groupBy(downloads.date)
      .orderBy(downloads.date)

    return data.map(d => ({ date: d.date!, count: Number(d.count) }))
  }

  public async getByPackage(packageName: string, start: string, end?: string): Promise<Downloads[] | null> {
    const [startDate, endDate] = getISODates(start, end)

    const data = await this.db
      .select({
        date: downloads.date,
        count: sum(downloads.count),
      })
      .from(downloads)
      .where(and(
        eq(downloads.timeslice, 'd'),
        between(downloads.date, startDate, endDate),
        eq(downloads.name, packageName)))
      .groupBy(downloads.date)
      .orderBy(downloads.date)

    return data.map(d => ({ date: d.date!, count: Number(d.count) }))
  }

  public async getByVersion(packageName: string, start: string, end?: string): Promise<DownloadsByVersion[] | null> {
    const [startDate, endDate] = getISODates(start, end)

    const data = await this.db
      .select({
        version: downloads.version,
        count: sum(downloads.count),
      })
      .from(downloads)
      .where(and(
        eq(downloads.timeslice, 'd'),
        between(downloads.date, startDate, endDate),
        eq(downloads.name, packageName)))
      .groupBy(downloads.version)
      .orderBy(downloads.version)

    return data.map(d => ({ version: d.version!, count: Number(d.count) }))
  }
}
