import { sql } from 'drizzle-orm'
import {
  boolean,
  customType,
  date,
  index,
  integer,
  jsonb,
  pgEnum,
  pgTable,
  primaryKey,
  serial,
  text,
  timestamp,
} from 'drizzle-orm/pg-core'

// Common columns
const timestamps = {
  created: timestamp().defaultNow().notNull(),
  updated: timestamp().defaultNow().notNull(),
}
const timestampsDeleted = {
  ...timestamps,
  deleted: timestamp(),
}

// Team package permissions
export const permissionEnum = pgEnum('permissions', ['r', 'w'])
// Time slices for downloads
export const timesliceEnum = pgEnum('timeslices', ['d', 'm', 'y', 't'])
// Event methods
export const methodEnum = pgEnum('methods', ['get', 'post', 'put', 'delete'])

// Binary columns
// https://github.com/drizzle-team/drizzle-orm/issues/298#issuecomment-3223856537
export const bytea = customType<{ data: Buffer, notNull: false, default: false }>({
  dataType() {
    return 'bytea'
  },
  fromDriver(value: unknown): Buffer<ArrayBufferLike> {
    if (value instanceof Buffer) {
      return value
    }

    if (typeof value === 'string') {
      return Buffer.from(value.replace(/\\x/g, ''), 'hex')
    }

    throw new Error(`Cannot convert type: ${typeof value} to buffer`)
  },
})

/**
 * Users
 */
export const users = pgTable('users', {
  id: serial().unique(),
  // user slug: mbtools
  user: text().primaryKey(),
  // Marc Bernard
  fullname: text(),
  // I build awesome open-source tools for SAP customers, partners, and developers
  description: text(),
  // mbtools@github.com
  email: text(),
  email_verified: boolean().notNull().default(false),
  // https://marcf.be
  homepage: text(),
  // https://github.com/mbtools
  github: text(),
  // Other links
  links: jsonb(),
  ...timestampsDeleted,
})

/**
 * User Secrets
 */
export const userSecrets = pgTable('user_secrets', {
  id: serial().unique(),
  user: text().primaryKey(),
  hash: text().notNull(),
  email: text().notNull(),
  ...timestampsDeleted,
})

/**
 * Roles
 */
export const roles = pgTable('roles', {
  id: serial().unique(),
  // admin, member, owner
  role: text().primaryKey(),
  description: text(),
  ...timestampsDeleted,
})

/**
 * Organizations
 */
export const orgs = pgTable('orgs', {
  id: serial().unique(),
  // org slug: @apm
  org: text().primaryKey(),
  // apm.to Inc.
  name: text(),
  // We are the company behind apm Package Manager for ABAP ðŸ“¦, the apm Website ðŸŒ, and the apm Registry ðŸ“‘
  description: text(),
  // https://apm.to
  homepage: text(),
  // https://github.com/abappm
  github: text(),
  // Other links
  links: jsonb(),
  ...timestampsDeleted,
})

/**
 * Organization Members
 */
export const orgMembers = pgTable('org_members', {
  id: serial().unique(),
  org_id: integer().references(() => orgs.id),
  user_id: integer().references(() => users.id),
  role_id: integer().references(() => roles.id),
  ...timestampsDeleted,
},
t => [primaryKey({ columns: [t.org_id, t.user_id, t.role_id] })],
)

/**
 * Teams
 */
export const teams = pgTable('teams', {
  id: serial().unique(),
  org_id: integer().references(() => orgs.id),
  team: text().notNull(),
  description: text(),
  ...timestampsDeleted,
},
t => [primaryKey({ columns: [t.org_id, t.team] })],
)

/**
 * Team Members
 */
export const teamMembers = pgTable('team_members', {
  id: serial().unique(),
  org_id: integer().references(() => orgs.id),
  team_id: integer().references(() => teams.id),
  user_id: integer().references(() => users.id),
  ...timestampsDeleted,
},
t => [primaryKey({ columns: [t.org_id, t.team_id, t.user_id] })],
)

/**
 * Team Packages
 */
export const teamPackages = pgTable('team_packages', {
  id: serial().unique(),
  org_id: integer().references(() => orgs.id),
  team_id: integer().references(() => teams.id),
  package_id: integer().references(() => packages.id),
  permission: permissionEnum().$type<string>(),
  ...timestampsDeleted,
},
t => [primaryKey({ columns: [t.org_id, t.team_id, t.package_id] })],
)

/**
 * Secrets
 */
export const secrets = pgTable('secrets', {
  name: text().primaryKey(),
  value: text().notNull(),
  ...timestamps,
})

/**
 * Tokens
 */
export const tokens = pgTable('tokens', {
  id: serial().unique(),
  user: text().notNull(),
  key: text().notNull(),
  token: text().notNull(),
  cidr: text().array(),
  readonly: boolean().notNull(),
  ...timestamps,
},
t => [primaryKey({ columns: [t.user, t.key] })],
)

/**
 * Local Packages
 */
export const localPackages = pgTable('local_packages', {
  id: serial().unique(),
  org_id: integer().references(() => orgs.id),
  name: text().notNull(),
  ...timestamps,
},
t => [primaryKey({ columns: [t.org_id, t.name] })],
)

/**
 * Packages
 */
export const packages = pgTable('packages', {
  id: serial().unique(),
  org_id: integer().references(() => orgs.id),
  name: text().notNull(),
  json: jsonb().notNull(),
  ...timestampsDeleted,
},
t => [
  primaryKey({ columns: [t.org_id, t.name] }),
  // Fulltext search on JSON fields (name, description, keywords)
  index('package_search_index').using('gin', sql`to_tsvector('english',
    coalesce(${t.json}->>'name', '') || ' ' ||
    coalesce(${t.json}->>'description', '') || ' ' ||
    coalesce(${t.json}->>'keywords', ''))`)],
)

/**
 * Readmes
 */
export const readmes = pgTable('readmes', {
  id: serial().unique(),
  org_id: integer().references(() => orgs.id),
  name: text().notNull(),
  version: text().notNull(),
  markdown: text().notNull(),
  ...timestampsDeleted,
},
t => [
  primaryKey({ columns: [t.org_id, t.name, t.version] }),
  index('readme_search_index').using('gin', sql`to_tsvector('english', ${t.markdown})`)],
)

