/**
 * Downloads
 */
export const downloads = pgTable('downloads', {
  id: serial().unique(),
  org_id: integer().references(() => orgs.id),
  timeslice: timesliceEnum().notNull().$type<string>(),
  year: integer(),
  month: integer(),
  date: date(),
  name: text(),
  version: text(),
  count: integer().notNull(),
},
t => [primaryKey({ columns: [t.org_id, t.timeslice, t.year, t.month, t.date, t.name, t.version] })],
)

/**
 * Events
 */
export const events = pgTable('events', {
  id: serial().unique(),
  event: text().notNull(),
})

/**
 * Event Log
 */
export const eventLog = pgTable('event_log', {
  id: serial().unique(),
  org_id: integer().references(() => orgs.id),
  timestamp: timestamp().defaultNow().notNull(),
  user_id: integer().references(() => users.id),
  method: methodEnum().notNull(),
  event_id: integer().references(() => events.id),
  name: text(),
  version: text(),
},
t => [primaryKey({ columns: [t.org_id, t.timestamp, t.user_id] })],
)

