import buildDebug from 'debug'
import { and, eq, isNull } from 'drizzle-orm'
import { errorUtils } from '@verdaccio/core'
import { Logger } from '@verdaccio/types'

import { type Database } from '../db'
import { events, eventLog, users } from '../db/schema'
import { TenantService } from './tenant'

const debug = buildDebug('verdaccio:plugin:storage:sql')

export const ANONYMOUS_USER = '#'

export type Method = 'get' | 'post' | 'put' | 'delete'

export class EventLogService {
  private db: Database
  private logger: Logger
  private tenant: TenantService
  private eventCache: Map<string, number>
  private userCache: Map<string, number>

  public constructor(database: Database, logger: Logger) {
    this.db = database
    this.logger = logger
    this.tenant = new TenantService(database, logger)
    this.eventCache = new Map()
    this.userCache = new Map()
  }

  public async log(user: string, method: Method, event: string, name: string, version?: string): Promise<void> {
    const org_id = await this.tenant.get(name)

    const data = {
      org_id,
      user_id: await this.getUserId(user),
      method,
      event_id: await this.getEventId(event),
      name,
      version,
    }

    try {
      await this.db
        .insert(eventLog)
        .values(data)

      debug('activity logged successfully')
    }
    catch (error) {
      debug('activity error: %o', error)
    }
  }

  private async getUserId(user: string): Promise<number> {
    if (!user || user === '') {
      return this.getUserId(ANONYMOUS_USER)
    }

    if (this.userCache.has(user)) {
      return this.userCache.get(user)!
    }

    const [ids] = await this.db
      .select({ id: users.id })
      .from(users)
      .where(and(eq(users.user, user), isNull(users.deleted)))

    if (!ids) {
      throw errorUtils.getNotFound(`user "${user}" not found`)
    }

    const id = ids.id
    this.userCache.set(user, id)
    return id
  }

  private async getEventId(event: string): Promise<number> {
    if (this.eventCache.has(event)) {
      return this.eventCache.get(event)!
    }

    const [ids] = await this.db
      .select({ id: events.id })
      .from(events)
      .where(eq(events.event, event))

    if (!ids) {
      throw errorUtils.getNotFound(`event "${event}" not found`)
    }

    const id = ids.id
    this.eventCache.set(event, id)
    return id
  }
}
