import buildDebug from 'debug'
import { and, eq, isNull, sql } from 'drizzle-orm'
import { Readable, Writable } from 'stream'
import { errorUtils } from '@verdaccio/core'
import { Logger } from '@verdaccio/types'

import { type Database } from '../db'
import { tarballs } from '../db/schema'
import { TenantService } from './tenant'
import { getPackageInfoFromFilename } from './utils'

const debug = buildDebug('verdaccio:plugin:storage:sql')

const CHUNK_SIZE = 256 * 1024 // 256KB

export class TarballService {
  private db: Database
  private logger: Logger
  private tenant: TenantService

  public constructor(database: Database, logger: Logger) {
    this.db = database
    this.logger = logger
    this.tenant = new TenantService(database, logger)
  }

  public async exists(name: string): Promise<boolean> {
    const org_id = await this.tenant.get(name)

    const count = await this.db
      .$count(tarballs,
        and(
          eq(tarballs.org_id, org_id),
          eq(tarballs.name, name),
          isNull(tarballs.deleted),
        ))

    const exists = count > 0
    debug('tarball exists: %o', exists)
    return exists
  }

  public async read(filename: string, { signal }: { signal: AbortSignal }): Promise<Readable> {
    const { name } = getPackageInfoFromFilename(filename)
    const org_id = await this.tenant.get(name)

    const [tarballData] = await this.db
      .select({
        data: tarballs.data,
        size: tarballs.size,
      })
      .from(tarballs)
      .where(and(
        eq(tarballs.org_id, org_id),
        eq(tarballs.filename, filename),
        isNull(tarballs.deleted),
      ))

    if (!tarballData) {
      throw errorUtils.getNotFound(`Tarball not found: ${filename}`)
    }

    const readable = new Readable({
      read() {
        for (let i = 0; i < tarballData.data.length; i += CHUNK_SIZE) {
          const dataChunk = tarballData.data.subarray(i, i + CHUNK_SIZE)
          this.push(dataChunk)
          if (dataChunk.length !== CHUNK_SIZE) {
            break
          }
        }
        this.push(null)
      },
    })

    signal.addEventListener('abort', () => {
      debug('aborting read stream')
      tarballData.data = Buffer.alloc(0)
      readable.destroy()
    })

    readable.on('open', () => {
      debug('opening read stream')
      readable.emit('content-size', tarballData.size)
    })

    process.nextTick(() => {
      readable.emit('open')
    })

    debug('returning readable stream')
    return readable
  }

  public async write(filename: string, { signal }: { signal: AbortSignal }): Promise<Writable> {
    const { name, version } = getPackageInfoFromFilename(filename)
    const org_id = await this.tenant.get(name)

    const chunks: Buffer[] = []

    const writable = new Writable({
      write(chunk, encoding, callback) {
        chunks.push(Buffer.from(chunk))
        callback()
      },
    })

    signal.addEventListener('abort', () => {
      debug('aborting write stream')
      writable.destroy()
    })

    process.nextTick(() => {
      debug('opening write stream')
      writable.emit('open')
    })

    writable.on('finish', async () => {
      const data = Buffer.concat(chunks)

      const tarballData = {
        org_id,
        name,
        version,
        filename,
        data,
        size: data.length,
      }

      try {
        await this.db
          .insert(tarballs)
          .values(tarballData)
          .onConflictDoUpdate({
            target: [tarballs.org_id, tarballs.name, tarballs.version],
            set: {
              data: sql`excluded.data`,
              size: sql`excluded.size`,
              updated: new Date(),
              deleted: null,
            },
          })

        debug('tarball written successfully')
      }
      catch (err) {
        debug('write error: %o', err)
        tarballData.data = Buffer.alloc(0)
        writable.destroy(err as Error)
      }
    })

    writable.on('error', (err) => {
      debug('write stream error: %o', err)
      writable.destroy(err)
    })

    debug('returning writable stream')
    return writable
  }

  public async delete(filename: string): Promise<void> {
    const { name, version } = getPackageInfoFromFilename(filename)
    const org_id = await this.tenant.get(name)

    // Note: This is a "soft delete" and does not delete the tarball from the database.
    // The data remains in the large object storage.
    try {
      await this.db
        .update(tarballs)
        .set({ deleted: new Date() })
        .where(and(
          eq(tarballs.org_id, org_id),
          eq(tarballs.name, name),
          eq(tarballs.version, version),
          eq(tarballs.filename, filename),
        ))

      debug('tarball deleted')
    }
    catch (error) {
      debug('error: %o', error)
      throw errorUtils.getInternalError(`Error deleting tarball: ${error}`)
    }
  }

  public async remove(name: string): Promise<void> {
    const org_id = await this.tenant.get(name)

    // Note: This is a "soft delete" and does not delete the tarball from the database.
    // The data remains in the large object storage.
    try {
      await this.db
        .update(tarballs)
        .set({ deleted: new Date() })
        .where(and(
          eq(tarballs.org_id, org_id),
          eq(tarballs.name, name),
        ))

      debug('all tarballs removed')
    }
    catch (error) {
      debug('error: %o', error)
      throw errorUtils.getInternalError(`Error removing all tarballs: ${error}`)
    }
  }
}
