import buildDebug from 'debug'
import { errorUtils } from '@verdaccio/core'
import { Logger, TokenFilter, Token as VerdaccioToken } from '@verdaccio/types'

import { type Database } from '../db'
import { tokens } from '../db/schema'
import { and, eq } from 'drizzle-orm'

const debug = buildDebug('verdaccio:plugin:storage:sql')

export interface Token {
  user: string
  token: string
  key: string
  cidr?: string[] | null
  readonly: boolean
  created: Date
  updated?: Date
}

export class TokenService {
  private db: Database
  private logger: Logger

  public constructor(database: Database, logger: Logger) {
    this.db = database
    this.logger = logger
  }

  public async read({ user }: TokenFilter): Promise<VerdaccioToken[]> {
    const userTokens = await this.db
      .select({
        user: tokens.user,
        key: tokens.key,
        token: tokens.token,
        cidr: tokens.cidr,
        readonly: tokens.readonly,
        created: tokens.created,
        updated: tokens.updated,
      })
      .from(tokens)
      .where(eq(tokens.user, user))
      .orderBy(tokens.user, tokens.key)

    if (!userTokens || userTokens.length === 0) {
      debug('token not found')
      return []
    }

    const verdaccioTokens = userTokens.map(token => TokenService.toVerdaccioToken(token))
    debug('token read successfully')
    return verdaccioTokens
  }

  public async save(token: VerdaccioToken): Promise<void> {
    try {
      await this.db
        .insert(tokens)
        .values(TokenService.fromVerdaccioToken(token))

      debug('token saved successfully')
    }
    catch (error) {
      debug('tokens error: %o', error)
      throw errorUtils.getInternalError(`Error saving token: ${error}`)
    }
  }

  public async delete(user: string, key: string): Promise<void> {
    const exists = await this.db
      .select()
      .from(tokens)
      .where(eq(tokens.user, user))

    if (!exists || exists.length === 0) {
      throw new Error('user not found')
    }

    try {
      await this.db
        .delete(tokens)
        .where(and(eq(tokens.user, user), eq(tokens.key, key)))

      debug('token deleted successfully')
    }
    catch (error) {
      debug('tokens error: %o', error)
      throw errorUtils.getInternalError(`Error deleting token: ${error}`)
    }
  }

  private static toVerdaccioToken(token: Token): VerdaccioToken {
    return {
      ...token,
      cidr: token.cidr || undefined,
      created: token.created && token.created.getTime(),
      updated: token.updated && token.updated.getTime(),
    }
  }

  private static fromVerdaccioToken(token: VerdaccioToken): Token {
    return {
      ...token,
      created: new Date(token.created),
      updated: (token.updated && new Date(token.updated)) || undefined,
    }
  }
}
