import buildDebug from 'debug'
import { errorUtils } from '@verdaccio/core'
import { Logger } from '@verdaccio/types'

import { type Database } from '../db'
import { secrets } from '../db/schema'
import { eq, sql } from 'drizzle-orm'

const debug = buildDebug('verdaccio:plugin:storage:sql')

const SECRET_NAME = 'verdaccio'

export class VerdaccioSecretService {
  private db: Database
  private logger: Logger

  public constructor(database: Database, logger: Logger) {
    this.db = database
    this.logger = logger
  }

  public async set(secret: string): Promise<string> {
    try {
      await this.db
        .insert(secrets)
        .values({ name: SECRET_NAME, value: secret })
        .onConflictDoUpdate({
          target: [secrets.name],
          set: { value: sql`excluded.value`, updated: new Date() },
        })

      debug('secret set successfully')
    }
    catch (error) {
      debug('secrets error: %o', error)
      throw errorUtils.getInternalError(`Error setting secret: ${error}`)
    }

    return secret
  };

  public async get(): Promise<string> {
    const [secret] = await this.db
      .select({ value: secrets.value })
      .from(secrets)
      .where(eq(secrets.name, SECRET_NAME))

    return secret ? secret.value : ''
  };
}
