import buildDebug from 'debug'
import { and, eq, sql } from 'drizzle-orm'
import { errorUtils } from '@verdaccio/core'
import { Logger } from '@verdaccio/types'

import { type Database } from '../db'
import { localPackages } from '../db/schema'
import { TenantService } from './tenant'

const debug = buildDebug('verdaccio:plugin:storage:sql')

export class LocalPackagesService {
  private db: Database
  private logger: Logger
  private tenant: TenantService

  public constructor(database: Database, logger: Logger) {
    this.db = database
    this.logger = logger
    this.tenant = new TenantService(database, logger)
  }

  public async add(name: string): Promise<void> {
    debug('add local package %o', name)
    const org_id = await this.tenant.get(name)

    try {
      await this.db
        .insert(localPackages)
        .values({ org_id, name })
        .onConflictDoUpdate({
          target: [localPackages.org_id, localPackages.name],
          set: { updated: new Date() },
        })

      debug('package %o has been added', name)
    }
    catch (error) {
      debug('upsert error: %o', error)
      throw errorUtils.getInternalError(`Error adding local package: ${error}`)
    }
  };

  public async remove(name: string): Promise<void> {
    debug('remove local package %o', name)
    const org_id = await this.tenant.get(name)

    try {
      await this.db
        .delete(localPackages)
        .where(and(eq(localPackages.org_id, org_id), eq(localPackages.name, name)))

      debug('package %o has been removed', name)
    }
    catch (error) {
      debug('delete error: %o', error)
      throw errorUtils.getInternalError(`Error deleting local package: ${error}`)
    }
  };

  public async get(): Promise<string[]> {
    debug('get full list of package')

    const names = await this.db
      .select({ name: localPackages.name })
      .from(localPackages)
      .orderBy(localPackages.name)
    debug('list of %o packages has been fetched', names ? names.length : 0)
    return names.map(row => row.name)
  };

  public async clean(): Promise<void> {
    debug('clean all local packages')

    await this.db
      .execute(sql`TRUNCATE TABLE local_packages`)
  };
}
