import { eq } from 'drizzle-orm'
import { errorUtils } from '@verdaccio/core'
import { Logger } from '@verdaccio/types'

import { type Database } from '../db'
import { orgs } from '../db/schema'

// Needs to be seeded in the database (see db/seeds/data/orgs.json)
export const PUBLIC_PACKAGES = '@'

export class OrgService {
  private db: Database
  private logger: Logger
  private orgCache: Map<string, number>

  public constructor(database: Database, logger: Logger) {
    this.db = database
    this.logger = logger
    this.orgCache = new Map()
  }

  public async getOrgId(name: string): Promise<number> {
    const [ids] = await this.db
      .select({ id: orgs.id })
      .from(orgs)
      .where(eq(orgs.org, name))

    if (!ids) {
      throw errorUtils.getNotFound(`organization "${name}" not found`)
    }

    return ids.id
  };

  public async getOrgIdfromPackage(name: string): Promise<number> {
    // debug('get org id from package %o', name);
    if (this.orgCache.has(name)) {
      return this.orgCache.get(name)!
    }

    let orgId
    if (name.includes('/')) {
      orgId = await this.getOrgId(name.split('/')[0])
    }
    else {
      orgId = await this.getOrgId(PUBLIC_PACKAGES)
    }

    // debug('org id is %o', orgId);
    this.orgCache.set(name, orgId)
    return orgId
  };
}
