import buildDebug from 'debug'
import { and, eq, isNull, inArray, sql } from 'drizzle-orm'
import { errorUtils, searchUtils } from '@verdaccio/core'
import { Callback, Logger, Manifest } from '@verdaccio/types'

import { type Database } from '../db'
import { distTags, packages, readmes } from '../db/schema'
import { TenantService } from './tenant'
import {
  clearReadmesFromManifest,
  getReadmesFromManifest,
  mergeReadmesIntoManifest,
  unescapeHtmlEntities,
} from './utils'

const debug = buildDebug('verdaccio:plugin:storage:sql')

export type DistTagsList = { [name: string]: { [tag: string]: string } }

export class PackageService {
  private db: Database
  private logger: Logger
  private tenant: TenantService

  public constructor(database: Database, logger: Logger) {
    this.db = database
    this.logger = logger
    this.tenant = new TenantService(database, logger)
  }

  public async exists(name: string): Promise<boolean> {
    const org_id = await this.tenant.get(name)

    const count = await this.db
      .$count(packages,
        and(
          eq(packages.org_id, org_id),
          eq(packages.name, name),
          isNull(packages.deleted),
        ))

    const exists = count > 0
    debug('package exists: %o', exists)
    return exists
  };

  public async create(name: string, manifest: Manifest): Promise<void> {
    await this.save(name, manifest)
    debug('package created successfully')
  };

  public async read(name: string, noThrow?: boolean): Promise<Manifest> {
    const org_id = await this.tenant.get(name)

    const [packageJson] = await this.db
      .select({ json: packages.json })
      .from(packages)
      .where(and(
        eq(packages.org_id, org_id),
        eq(packages.name, name),
        isNull(packages.deleted),
      ))

    if (!packageJson) {
      if (noThrow) {
        return {} as Manifest
      }
      throw errorUtils.getNotFound('package.json not found')
    }

    const manifest = JSON.parse(unescapeHtmlEntities(JSON.stringify(packageJson.json))) as Manifest

    const markdown = await this.db
      .select({ version: readmes.version, markdown: readmes.markdown })
      .from(readmes)
      .where(and(
        eq(readmes.org_id, org_id),
        eq(readmes.name, name),
        isNull(readmes.deleted),
      ))

    const manifestMerged = mergeReadmesIntoManifest(manifest, markdown)
    debug('package read successfully')
    return manifestMerged
  };

  public async save(name: string, manifest: Manifest): Promise<void> {
    const org_id = await this.tenant.get(name)

    await this.db.transaction(async (tx) => {
      // Save dist-tags separately for efficient determination of the latest version
      const tags = manifest['dist-tags']

      const distTagsData = Object.entries(tags).map(([tag, version]) => ({
        org_id,
        tag,
        name,
        version,
      }))

      try {
        await tx
          .delete(distTags)
          .where(and(
            eq(distTags.org_id, org_id),
            eq(distTags.name, name),
          ))

        debug('dist-tags deleted successfully')
      }
      catch (error) {
        // Ignore error if no dist-tags to delete
        debug('dist-tags delete error: %o', error)
      }

      if (distTagsData && distTagsData.length > 0) {
        try {
          await tx
            .insert(distTags)
            .values(distTagsData)

          debug('dist-tags saved successfully')
        }
        catch (error) {
          debug('dist-tags insert error: %o', error)
          tx.rollback()
        }
      }

      // Save markdown separately for efficient full text search
      const markdown = getReadmesFromManifest(manifest)

      const readmesData = markdown.map(row => ({
        org_id,
        name,
        version: row.version,
        markdown: row.markdown,
        deleted: null,
      }))

      if (readmesData && readmesData.length > 0) {
        try {
          await tx
            .insert(readmes)
            .values(readmesData)
            .onConflictDoUpdate({
              target: [readmes.org_id, readmes.name, readmes.version],
              set: { markdown: sql`excluded.markdown`, updated: new Date(), deleted: null },
            })

          debug('readmes saved successfully')
        }
        catch (error) {
          debug('readmes error: %o', error)
          tx.rollback()
        }
      }

      const manifestClean = clearReadmesFromManifest(manifest)

      try {
        await tx
          .insert(packages)
          .values({
            org_id,
            name,
            json: manifestClean,
          })
          .onConflictDoUpdate({
            target: [packages.org_id, packages.name],
            set: { json: sql`excluded.json`, updated: new Date(), deleted: null },
          })

        debug('package saved successfully')
      }
      catch (error) {
        debug('packages error: %o', error)
        tx.rollback()
      }
    })
  };

  public async update(name: string, handleUpdate: Callback): Promise<Manifest> {
    // Potentially read the package with lock, then unlock after update
    // but it's unlikely to have simultaneous updates (famous last words)
    const manifest = await this.read(name, true)
    const manifestUpdated = await handleUpdate(manifest)
    debug('package updated successfully')
    return manifestUpdated
  };

  public async delete(name: string): Promise<void> {
    const org_id = await this.tenant.get(name)

    await this.db.transaction(async (tx) => {
      try {
        await tx
          .update(packages)
          .set({ deleted: new Date() })
          .where(and(
            eq(packages.org_id, org_id),
            eq(packages.name, name),
          ))

        debug('package deleted successfully')
      }
      catch (error) {
        debug('packages error: %o', error)
        tx.rollback()
      }

      try {
        await tx
          .update(readmes)
          .set({ deleted: new Date() })
          .where(and(
            eq(readmes.org_id, org_id),
            eq(readmes.name, name),
          ))

        debug('readmes deleted successfully')
      }
      catch (error) {
        debug('readmes error: %o', error)
        tx.rollback()
      }

      try {
        await tx
          .delete(distTags)
          .where(and(
            eq(distTags.org_id, org_id),
            eq(distTags.name, name),
          ))

        debug('dist-tags deleted successfully')
      }
      catch (error) {
        // Ignore error if no dist-tags to delete
        debug('dist-tags error: %o', error)
      }
    })
  };

  public static async search(
    db: Database,
    query: searchUtils.SearchQuery,
  ): Promise<searchUtils.SearchItemPkg[]> {
    // FUTURE full text search
    // https://orm.drizzle.team/docs/guides/postgresql-full-text-search

    // TODO: cache and return requested slice of the results
    const results: searchUtils.SearchItemPkg[] = []

    const rows = await db
      .select({
        name: packages.name,
        json: packages.json,
        updated: packages.updated,
        rank: sql<number>`ts_rank(
          to_tsvector('english',
            coalesce(${packages.json}->>'name', '') || ' ' ||
            coalesce(${packages.json}->>'description', '') || ' ' ||
            coalesce(${packages.json}->>'keywords', '')
          ),
          plainto_tsquery('english', ${query.text})
        )`,
      })
      .from(packages)
      .where(sql`to_tsvector('english',
        coalesce(${packages.json}->>'name', '') || ' ' ||
        coalesce(${packages.json}->>'description', '') || ' ' ||
        coalesce(${packages.json}->>'keywords', '')
      ) @@ plainto_tsquery('english', ${query.text}) AND ${packages.deleted} IS NULL`)
      .orderBy(sql`rank DESC`)

    // TODO: full text search of readmes

    for (const row of rows) {
      // Use time to store the ranking
      // TODO: add rank to the result type in Verdaccio core
      results.push({
        name: row.name,
        scoped: row.name,
        path: undefined,
        time: row.rank,
      })
    }

    debug('%o packages found', results.length)

    if (!results || results.length === 0) {
      debug('no results found')
      return results
    }
    debug('%o results found', results.length)

    return results.slice(query.from, query.size)
  };

  public async getDistTags(names: string[]): Promise<DistTagsList> {
    const result: DistTagsList = {}

    const rows = await this.db
      .select({
        org_id: distTags.org_id,
        name: distTags.name,
        tag: distTags.tag,
        version: distTags.version,
      })
      .from(distTags)
      .where(inArray(distTags.name, names))
      .orderBy(distTags.name, distTags.tag)

    for (const row of rows) {
      const org_id = await this.tenant.get(row.name)

      if (row.org_id === org_id) {
        result[row.name][row.tag] = row.version
      }
    }

    return result
  }
}
