import { Logger } from '@verdaccio/types'

import { type Database } from '../db'
import { OrgService } from './org'

export class TenantService {
  private org: OrgService

  public constructor(database: Database, logger: Logger) {
    this.org = new OrgService(database, logger)
  }

  public async get(name: string): Promise<number> {
    // Tenant = Org
    // This is cached in the OrgService
    return await this.org.getOrgIdfromPackage(name)
  }
}
